import dayjs, { Dayjs } from "dayjs";
import localeData from 'dayjs/plugin/localeData';
import weekday from "dayjs/plugin/weekday";

import "dayjs/locale/zh-cn";
import "dayjs/locale/ja";
import "dayjs/locale/zh-hk";
import "dayjs/locale/zh-tw";

dayjs.extend(weekday);
dayjs.extend(localeData);

type DateControllerType<DateType> = {
  // Get
  getWeekDay: (value: DateType) => number;
  getSecond: (value: DateType) => number;
  getMinute: (value: DateType) => number;
  getHour: (value: DateType) => number;
  getDate: (value: DateType) => number;
  getMonth: (value: DateType) => number;
  getYear: (value: DateType) => number;
  getNow: () => DateType;
  getFixedDate: (fixed: string) => DateType;
  getEndDate: (value: DateType) => DateType;

  // Set
  addYear: (value: DateType, diff: number) => DateType;
  addMonth: (value: DateType, diff: number) => DateType;
  addDate: (value: DateType, diff: number) => DateType;
  setYear: (value: DateType, year: number) => DateType;
  setMonth: (value: DateType, month: number) => DateType;
  setDate: (value: DateType, date: number) => DateType;
  setHour: (value: DateType, hour: number) => DateType;
  setMinute: (value: DateType, minute: number) => DateType;
  setSecond: (value: DateType, second: number) => DateType;

  // Compare
  isAfter: (date1: DateType, date2: DateType) => boolean;
  isValidate: (date: DateType) => boolean;

  locale: {
    getWeekFirstDay: (locale: string) => number;
    getWeekFirstDate: (locale: string, value: DateType) => DateType;

    format: (locale: string, date: DateType, format: string) => string;

    getShortWeekDays: (locale: string) => string[];
    getShortMonths: (locale: string) => string[];
  };
};

export const DateController: DateControllerType<Dayjs> = {
  // get
  getNow: () => dayjs(),
  getFixedDate: (string) => dayjs(string, ["YYYY-M-DD", "YYYY-MM-DD"]),
  getEndDate: (date) => date.endOf("month"),
  getWeekDay: (date) => {
    const clone = date.locale("en");
    return clone.weekday() + clone.localeData().firstDayOfWeek();
  },
  getYear: (date) => date.year(),
  getMonth: (date) => date.month(),
  getDate: (date) => date.date(),
  getHour: (date) => date.hour(),
  getMinute: (date) => date.minute(),
  getSecond: (date) => date.second(),

  // set
  addYear: (date, diff) => date.add(diff, "year"),
  addMonth: (date, diff) => date.add(diff, "month"),
  addDate: (date, diff) => date.add(diff, "day"),
  setYear: (date, year) => date.year(year),
  setMonth: (date, month) => date.month(month),
  setDate: (date, num) => date.date(num),
  setHour: (date, hour) => date.hour(hour),
  setMinute: (date, minute) => date.minute(minute),
  setSecond: (date, second) => date.second(second),

  // Compare
  isAfter: (date1, date2) => date1.isAfter(date2),
  isValidate: (date) => date.isValid(),

  locale: {
    getWeekFirstDay: (locale) =>
      dayjs().locale(locale).localeData().firstDayOfWeek(),
    getWeekFirstDate: (locale, date) => date.locale(locale).weekday(0),
    getShortWeekDays: (locale) =>
      dayjs().locale(locale).localeData().weekdaysMin(),
    getShortMonths: (locale) =>
      dayjs().locale(locale).localeData().monthsShort(),
    format: (locale, date, format) => date.locale(locale).format(format),
  },
};

export type PanelMode = "year" | "month" | "date";

export const getCellDateDisabled = ({
  cellDate,
  mode,
  disabledDate,
}: {
  cellDate: Dayjs;
  mode: PanelMode;
  disabledDate?: (date: Dayjs) => boolean;
}): boolean => {
  if (!disabledDate) return false;

  // 判断cell在此range是否是disabled
  const getDisabledFromRange = (
    currentMode: PanelMode,
    start: number,
    end: number
  ) => {
    let current = start;
    while (current <= end) {
      let date: Dayjs;
      switch (currentMode) {
        case "date":
          date = DateController.setDate(cellDate, current);
          if (!disabledDate(date)) {
            return false;
          }
          break;

        case "month":
          date = DateController.setMonth(cellDate, current);
          if (
            !getCellDateDisabled({
              cellDate: date,
              mode: "month",
              disabledDate,
            })
          ) {
            return false;
          }
          break;

        case "year":
          date = DateController.setYear(cellDate, current);
          if (
            !getCellDateDisabled({
              cellDate: date,
              mode: "year",
              disabledDate,
            })
          ) {
            return false;
          }
          break;
      }
      current += 1;
    }
    return true;
  };
  switch (mode) {
    case "date":
      return disabledDate(cellDate);
    case "month":
      const startDate = 1;
      const endDate = DateController.getDate(
        DateController.getEndDate(cellDate)
      );
      return getDisabledFromRange("date", startDate, endDate);
    case "year":
      return getDisabledFromRange("month", 0, 11);
  }
};

export const getWeekStartDate = (locale: string, value: Dayjs) => {
  const weekFirstDay = DateController.locale.getWeekFirstDay(locale);
  const monthStartDate = DateController.setDate(value, 1);
  const startDateWeekDay = DateController.getWeekDay(monthStartDate);

  let alignStartDate = DateController.addDate(
    monthStartDate,
    weekFirstDay - startDateWeekDay
  );

  if (
    DateController.getMonth(alignStartDate) ===
      DateController.getMonth(value) &&
    DateController.getDate(alignStartDate) > 1
  ) {
    alignStartDate = DateController.addDate(alignStartDate, -7);
  }

  return alignStartDate;
};

export const leftPad = (
  str: string | number,
  length: number,
  fill: string = "0"
) => {
  let current = String(str);
  while (current.length < length) {
    current = `${fill}${str}`;
  }
  return current;
};

export function isNullEqual<T>(value1: T, value2: T): boolean | undefined {
  if (!value1 && !value2) {
    return true;
  }
  if (!value1 || !value2) {
    return false;
  }
  return undefined;
}

export const isSameYear = (year1: Dayjs | null, year2: Dayjs | null) => {
  const equal = isNullEqual(year1, year2);
  if (typeof equal === 'boolean') {
    return equal;
  }
  return DateController.getYear(year1!) === DateController.getYear(year2!);
};

export const isSameMonth = (month1: Dayjs | null, month2: Dayjs | null) => {
  const equal = isNullEqual(month1, month2);
  if (typeof equal === 'boolean') {
    return equal;
  }
  return (
    isSameYear(month1, month2) &&
    DateController.getMonth(month1!) === DateController.getMonth(month2!)
  );
};

export const isSameDate = (date1: Dayjs | null, date2: Dayjs | null): boolean => {
  const equal = isNullEqual(date1, date2);
  if (typeof equal === 'boolean') {
    return equal;
  }
  return (
    isSameYear(date1, date2) &&
    isSameMonth(date1, date2) &&
    DateController.getDate(date1!) === DateController.getDate(date2!)
  );
};

export const isSameTime = (
  time1: Dayjs | null,
  time2: Dayjs | null,
) => {
  const equal = isNullEqual(time1, time2);
  if (typeof equal === 'boolean') {
    return equal;
  }
  return (
    DateController.getHour(time1!) === DateController.getHour(time2!) &&
    DateController.getMinute(time1!) === DateController.getMinute(time2!) &&
    DateController.getSecond(time1!) === DateController.getSecond(time2!)
  );
}

export const isEqual = (value1: Dayjs | null, value2: Dayjs | null) => {
  const equal = isNullEqual(value1, value2);
  if (typeof equal === 'boolean') {
    return equal;
  }
  return isSameDate(value1, value2) && isSameTime(value1, value2);
};

export const dayjsLocaleMonthBeforeYearConfig: Record<string, boolean> = {
  'zh-cn': false,
  'zh-tw': false,
  'zh-hk': false,
  'ja': false,
  'en': true,
};